.section {
  padding: 100px calc(50% - 500px) 52px ;

  &__heading {
    margin: 0 auto 70px;
  }

  @media (max-width: 1040px) {
    & {
      padding-left: $gap;
      padding-right: $gap;
    }
  }
  
  @media (max-width: $small-screen) {
    & {
      padding-top: 64px;
    }

    &__heading {
      margin: 0 auto 30px;
    }
  }
}
